export const formatDater = (str) => {
    if (!str) return ''
    var date = new Date(str)
    var time = new Date().getTime() - date.getTime() //- =  = 
    if (time < 0) {
        return ''
    } else if ((time / 1000 < 30)) {
        return ''
    } else if (time / 1000 < 60) {
        return parseInt((time / 1000)) + ''
    } else if ((time / 60000) < 60) {
        return parseInt((time / 60000)) + ''
    } else if ((time / 3600000) < 24) {
        return parseInt(time / 3600000) + ''
    } else if ((time / 86400000) < 31) {
        return parseInt(time / 86400000) + ''
    } else if ((time / 2592000000) < 12) {
        return parseInt(time / 2592000000) + ''
    } else {
        return parseInt(time / 31536000000) + ''
    }
}

export const fiterTime = (nS) => {
    if ((nS === 0) | (nS === null) | !nS | (nS === "")) {
        return "";
    }

    function add0(m) {
        return m < 10 ? "0" + m : m;
    }

    var time = new Date(parseInt(nS) * 1000);
    var y = time.getFullYear();

    var m = time.getMonth() + 1;
    var d = time.getDate();
    var h = time.getHours();
    var mm = time.getMinutes();
    var s = time.getSeconds();
    return (
        y +
        "-" +
        add0(m) +
        "-" +
        add0(d) +
        " " +
        add0(h) +
        ":" +
        add0(mm) +
        ":" +
        add0(s)
    );
};

export const fiterDay = (nS) => {
    if ((nS === 0) | (nS === null) | !nS | (nS === "")) {
        return "";
    }

    function add0(m) {
        return m < 10 ? "0" + m : m;
    }

    var time = new Date(parseInt(nS) * 1000);
    var y = time.getFullYear();
    var m = time.getMonth() + 1;
    var d = time.getDate();
    var h = time.getHours();
    var mm = time.getMinutes();
    var s = time.getSeconds();
    return y + "-" + add0(m) + "-" + add0(d);
};

