export const formatDate = (value) => {
    var myDate = new Date(value * 1000);
    var year = myDate.getFullYear().toString();
    var month = myDate.getMonth() + 1;
    var day = myDate.getDate();
    if (month < 10) {
        month = "0" + month;
    }
    if (day < 10) {
        day = "0" + day;
    }
    return year + "-" + month + "-" + day;
};
