export const typeArr = (val) => {
    var type_id = "";
    if (val == null) {
        return (val = "");
    } else {
        var arr = val.split(",");
        for (var i = 0; i < arr.length; i++) {
            if (i >= 1) {
                type_id += ",";
            }
            if (arr[i] === "1") {
                type_id += "";
            } else if (arr[i] === "2") {
                type_id += "";
            } else if (arr[i] === "3") {
                type_id += "";
            } else if (arr[i] === "4") {
                type_id += "wap";
            }
        }
        var vab = type_id;
        type_id = "";
        return vab;
    }
};
