export const filterFileSize = (value) => {
    var result = "";
    if (value * 1 > 1024 * 1024) {
        result = ((value * 1) / 1024 / 1024).toFixed(2) + " MB";
    } else if (value * 1 > 1024) {
        result = ((value * 1) / 1024).toFixed(2) + " KB";
    } else {
        result = value + " byte";
    }
    return result;
};
